import 'ol/ol.css';
import Map from 'ol/Map';
import {Attribution} from 'ol/control';

import ImageWMS from 'ol/source/ImageWMS';
import {Image as ImageLayer} from 'ol/layer';

import View from 'ol/View';


var orka = new ImageLayer({
  source: new ImageWMS({
    url: 'https://www.orka-mv.de/geodienste/orkamv/wms',
    params: {'LAYERS': 'orkamv'},
    ratio: 1,
    attributions: 'Kartenbild  Hanse- und Universittsstadt Rostock (CC BY 4.0) | Kartendaten  OpenStreetMap (ODbL) und LkKfS-MV.'
  })
});

var attributionOrka = new Attribution({
  collapsed: false,
  collapsible: false,
  className: "ol-attribution"
});


var map1 = new Map({
  layers: [orka],
  target: 'map1',
  controls: [attributionOrka],
  view: new View({
    projection: 'EPSG:3857',
    center: [1344842.630278, 7203721.739080],
    zoom: 16,
    constrainResolution: true
  }),
});

